export default {
	data() {
		return {
			phone:'',													//手机号
			pwd:'',														//密码
			reg:'/^1[3456789]\d{9}$/',				//手机号正则
		};
	},
	methods:{
		// 登录验证
		startLogin() {
			console.log((/^1[3456789]\d{9}$/.test(this.phone)))
			if (!(/^1[3456789]\d{9}$/.test(this.phone))) {
				this.$queue.showToast('请输入手机号');
				return;
			}
			if (this.pwd.length < 1) {
				this.$queue.showToast('请输入密码');
				return;
			}
			this.toLogin();
		},
		// 登录
		toLogin(){
			this.$queue.showLoading('登录中...')
			this.$request.post('auth/login/login',{
				mobile: this.phone,
				password: this.pwd
			})
			.then(res=>{
				this.$queue.setData('token', res.data.token);
				uni.navigateTo({
					url:'/pages/workbench/index'
				})
			})
			.catch(res => {
				uni.hideLoading();
			});
		}
	}
}
